import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {environment} from '../../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})


export class DepartmentService {

  baseUrl =environment.baseURl;


  constructor(private http:HttpClient) {  
  }

  getDepartment(): Observable<any> {
    return this.http.get(this.baseUrl+'department');
  }

  addDepartment(obj): Observable<any> {
    return this.http.post(this.baseUrl+'department/',obj);
  }

  editDepartment(obj): Observable<any> {
  
    return this.http.put(this.baseUrl+'department/'+obj.id , obj);
  }

  deleteDepartment(id): Observable<any> {
    // Real End Point => tests/find/candidate
    return this.http.delete(this.baseUrl+'department/'+id);
  }
}
