import {Component, OnInit, ViewChild} from '@angular/core';
import {FormControl, FormGroup, NgForm, Validators , FormBuilder} from "@angular/forms";
import {ActivatedRoute} from "@angular/router";
import {Location} from "@angular/common";
import {UserService} from "../../../../../services/user-managment-services/user.service";
import {ToastrService} from 'ngx-toastr';
import { ProfileService } from 'services/user-managment-services/profile.service';
import { RoleService } from 'services/user-managment-services/role.service';
@Component({
  selector: 'app-update-user',
  templateUrl: './update-user.component.html',
  styleUrls: ['./update-user.component.scss']
})
export class UpdateUserComponent implements OnInit {

  @ViewChild('f') floatingLabelForm: NgForm;
  @ViewChild('vform') validationForm: FormGroup;
  regularForm: FormGroup;
  currentUser;
  profiles;
  userProfile;
  currentProfileId;
/* -------------------------------------------------------------------------- */
/*                                 Constructor                                */
/* -------------------------------------------------------------------------- */

  constructor( private route: ActivatedRoute, private _location: Location, private userService: UserService,private fb:FormBuilder,private toster:ToastrService,private profileService:ProfileService,private roleService:RoleService) {}

/* -------------------------------------------------------------------------- */
/*                             NgOnInit LifeCycle                             */
/* -------------------------------------------------------------------------- */

  ngOnInit() {
    this.regularForm = this.fb.group({
      'firstName': new FormControl(null,[Validators.required]),
      'lastName': new FormControl(null,[Validators.required]),
      'userName': new FormControl(null,[Validators.required]),
      'email': new FormControl(null, [Validators.required, Validators.email]),
      'phone':new FormControl(null,[Validators.required]),
      "gender": new FormControl(null,[Validators.required]),
      'description':new FormControl(null,[Validators.required]),
      'password': new FormControl(null, [Validators.required, Validators.minLength(4), Validators.maxLength(24)]),
      'confirmPassword': new FormControl(null, [Validators.required, Validators.minLength(4), Validators.maxLength(24)]),
      'profil':new FormControl(null,[Validators.required]),
    },{ validators:this.password.bind(this)});

    this.profileService.getProfiles().subscribe(profiles =>{
      this.profiles = profiles
    })
    this.userService.getUserById(this.route.snapshot.params['id']).subscribe(data => {
      this.currentProfileId = data.profile_id
        this.profileService.getProfileById(data.profile_id).subscribe(id =>{
        this.userProfile = id.title;
          this.currentUser = data;
          this.regularForm.setValue({firstName:data.first_name,lastName:data.last_name,userName:data.username,email:data.email,phone:data.phone,gender:data.gender,description:data.description,password:'',confirmPassword:'',profil:this.userProfile})
        },err =>{
          this.toster.error("Can't Get User Data Try Again Later",'', {
            timeOut: 3000,
            positionClass: 'toast-top-full-width',
            closeButton:true
            })
        })
      },err =>{
        this.toster.error("Can't Get This User Right Now",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
      });
  }

/* -------------------------------------------------------------------------- */
/*                            Go Back To Last Back                            */
/* -------------------------------------------------------------------------- */

  goback(){
    this._location.back()
  }

/* -------------------------------------------------------------------------- */
/*                                 Update User                                */
/* -------------------------------------------------------------------------- */

  updateUser() {
    if(this.regularForm.valid){
      let profileId = (this.profiles.find(data =>{
        return data.title === this.regularForm.value.profil
      })).id
      console.log(profileId)
      const NewUser = {
        "first_name": this.regularForm.value.firstName,
        "last_name": this.regularForm.value.lastName,
        "username": this.regularForm.value.userName,
        "email": this.regularForm.value.email,
        "phone":this.regularForm.value.phone,
        "gender": this.regularForm.value.gender,
        "description":this.regularForm.value.description,
        "password": this.regularForm.value.password,
        "repeat_password":this.regularForm.value.confirmPassword,
        "profile_id":profileId,
        "blocked":true,
        "id":this.route.snapshot.params['id']
      }
      this.userService.updateUser(NewUser).subscribe(data =>{
        this.toster.success("User Updated",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
        this.goback()
      },err =>{
        this.toster.error("Can't Update User Right Now",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
      })
    }
  }
/* -------------------------------------------------------------------------- */
/*                           Check Password Matching                          */
/* -------------------------------------------------------------------------- */

password(formGroup: FormGroup) {
  const { value: password } = formGroup.get('password');
  const { value: confirmPassword } = formGroup.get('confirmPassword');
  return password === confirmPassword ? null : { passwordNotMatch: true };
}
}
