import { Component, OnInit , ViewChild} from '@angular/core';
import { DatatableComponent } from "@swimlane/ngx-datatable";

import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Location } from "@angular/common";
import { DepartmentService } from 'services/department/department.service';
import { data } from 'app/shared/data/smart-data-table';
import Swal from 'sweetalert2';
declare let swal: any;

@Component({
  selector: 'app-departments',
  templateUrl: './departments.component.html',
  styleUrls: ['./departments.component.scss']
})
export class DepartmentsComponent implements OnInit {

  departmentForm: FormGroup;
  departmentEditForm: FormGroup;
  closeResult: string;

  editId

  rows = [];
  resetRows = [];
  temp = [];
  departmentList=[]

  // Table Column Titles
  columns = [
    { prop: "department" },
    { prop: "country" },
    { prop: "city" },
    { prop: "date" },
    /* { prop: "Compatibilty of Matching" }, */
  ];
  @ViewChild(DatatableComponent) table: DatatableComponent;

  constructor(
    private formBuilder: FormBuilder,
    private modalService: NgbModal,
    private toster: ToastrService,
    private _location: Location,
    private departmenService: DepartmentService,
  ) { }

  ngOnInit(): void {
    this.getDepartments();

    this.departmentForm = this.formBuilder.group(
      {
        department: ["", Validators.required],
        country: ["", Validators.required],
        city: ["", Validators.required],
      });
    this.departmentEditForm = this.formBuilder.group(
      {
        department: ["", Validators.required],
        country: ["", Validators.required],
        city: ["", Validators.required],
      });
  }



  /* -------------------------------------------------------------------------- */
  /*                           Modal Functions start                          */
  /* -------------------------------------------------------------------------- */
  open(content) {
    this.modalService.open(content, { size: 'lg' }).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }


  // This function is used in open
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  /* -------------------------------------------------------------------------- */
  /*                                Submit Modal Form                           */
  /* -------------------------------------------------------------------------- */
  onSubmit(form) {
    console.log(form);
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    var date = mm + '-' + dd + '-' + yyyy;
    console.log(date);

    var objDepartment={
      
      department: form.value.department,
      city: form.value.city,
      country: form.value.country,
      date: date,
    } 

    this.departmenService.addDepartment(objDepartment).subscribe(
      (data) => {
        console.log(data);
        this.rows = data;
        this.resetRows = this.rows
        this.departmentList = data

        this.toster.success(" added successfully", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
        this.getDepartments();
        this.departmentForm.reset();
        this.modalService.dismissAll();
      },
      (err) => {
        this.toster.error("We Have Problem Please Try Again", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }

  goback() {
    this._location.back();
  }



  /* -------------------------------------------------------------------------- */
  /*                                   dataTabel                                */
  /* -------------------------------------------------------------------------- */

  updateFilter(event) {
    const val = event.target.value;
    console.log(val);

    this.rows = this.resetRows
    // filter our data
    let temp = this.rows.filter(function (e) {
      return e["department"].includes(val) || e["department"].includes(val) 
    });
    console.log(temp);
    console.log(this.rows);


    // update the rows
    this.rows = temp
    if (val == "") {
      this.rows = this.resetRows
    }
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0; 
  }



  getDepartments() {
    this.departmenService.getDepartment().subscribe(
      (data) => {
        console.log(data);

        this.departmentList = data;
        this.rows = data;
        this.resetRows = this.rows
      },
      (err) => {
        this.toster.error("We Have Problem Please Try Again", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }

  deleteDepartment(value){
        // console.log(data);
        Swal.fire({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
          if (result.value) {
            Swal.fire(
              'Deleted!',
              'Job has been deleted.',
              'success'
            )
            this.departmenService.deleteDepartment(value).subscribe(
              (data) => {
                console.log(data);
                this.rows = data;
                this.departmentList = data
                this.getDepartments();
              },
              (err) => {
                this.toster.error("We Have Problem Please Try Again", "", {
                  timeOut: 3000,
                  positionClass: "toast-top-full-width",
                  closeButton: true,
                });
              }
            );
          }
        })
  }



  editDepartmentVal(id){
    console.log(id);
    let selectedDepartment = this.departmentList.filter(x=>x.id == id)
    console.log(selectedDepartment[0]);
    this.editId =selectedDepartment[0].id 
     this.departmentEditForm.patchValue({
       department:selectedDepartment[0].department , 
      city:selectedDepartment[0].city, 
      country:selectedDepartment[0].country
    })
  }

  onSubmitEdit(form){
   
    console.log(form);
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    var date = mm + '-' + dd + '-' + yyyy;
    console.log(date);

    var objDepartmentEdit={
      
      id:this.editId,
      department: form.value.department,
      city: form.value.city,
      country: form.value.country,
      date: date,
    } 
    console.log(objDepartmentEdit);
    

    this.departmenService.editDepartment(objDepartmentEdit).subscribe(
      (data) => {
        console.log(data);
        this.rows = data;
        this.resetRows = this.rows
        this.departmentList = data

        this.toster.success(" added successfully", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
        this.getDepartments();
        this.departmentForm.reset();
        this.modalService.dismissAll();
      },
      (err) => {
        this.toster.error("We Have Problem Please Try Again", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }


}

