import { Component, OnInit } from '@angular/core';
import { UserService } from 'services/user-managment-services/user.service';
import { ActivatedRoute } from '@angular/router';
import { ProfileService } from 'services/user-managment-services/profile.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-user-profile-details',
  templateUrl: './user-profile-details.component.html',
  styleUrls: ['./user-profile-details.component.scss']
})
export class UserProfileDetailsComponent implements OnInit {
  currentPage: string = "About"
  currentUser;
  currentUserProfile;

/* -------------------------------------------------------------------------- */
/*                                 Constructor                                */
/* -------------------------------------------------------------------------- */

  constructor(private userService:UserService,private currentRoute:ActivatedRoute,private profileService:ProfileService,private _location:Location) { }

/* -------------------------------------------------------------------------- */
/*                          NgonInit Life Cycle Hook                          */
/* -------------------------------------------------------------------------- */

  ngOnInit(): void {
    this.userService.getUserById(this.currentRoute.snapshot.params['id']).subscribe( data =>{
      this.profileService.getProfileById(data.profile_id).subscribe(profile =>{
        this.currentUserProfile = profile.title
        this.currentUser = data
      })
    })
  } 

/* -------------------------------------------------------------------------- */
/*                             Return To Last page                            */
/* -------------------------------------------------------------------------- */

goback(){
  this._location.back()
}
}
