import {Component, OnInit, ViewChild} from '@angular/core';
import {DatatableComponent} from "@swimlane/ngx-datatable";
import {TranslateService} from "@ngx-translate/core";
import { RoleService } from 'services/user-managment-services/role.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-list-roles',
  templateUrl: './list-roles.component.html',
  styleUrls: ['./list-roles.component.scss']
})
export class ListRolesComponent implements OnInit {

  FilterOptionLabel = 'Filter Option';
  FilterPlaceHolder = 'Type to filter the name column...';

  rows = [];

  temp = [];

  // Table Column Titles
  columns = [

    { prop: 'title' },
    { prop: 'description' },
  ];
  @ViewChild(DatatableComponent) table: DatatableComponent;

/* -------------------------------------------------------------------------- */
/*                           Constructor Life Cycle                           */
/* -------------------------------------------------------------------------- */

  constructor(public translate: TranslateService,private rolesService:RoleService,private route:Router,private router:ActivatedRoute,private toster:ToastrService) {}
  
/* -------------------------------------------------------------------------- */
/*                             ngOninit Life Cycle                            */
/* -------------------------------------------------------------------------- */

ngOnInit(): void {
 this.displayRoles()
}


  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.temp.filter(function (d) {
      return d.Libell√©.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rows = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

/* -------------------------------------------------------------------------- */
/*                            navigate To edit ROle                           */
/* -------------------------------------------------------------------------- */

  getElement(data){
    this.route.navigate(['/pages','login','list-roles','update-role',data])
  }

/* -------------------------------------------------------------------------- */
/*                                 Delete User                                */
/* -------------------------------------------------------------------------- */

  deleteRole(data){
    this.rolesService.deleterole(data).subscribe(data => {
      this.toster.success("Role Deleted",'', {
        timeOut: 3000,
        positionClass: 'toast-top-full-width',
        closeButton:true
        })
        this.displayRoles()
    },err =>{
      this.toster.error("Wrong Move",'', {
        timeOut: 3000,
        positionClass: 'toast-top-full-width',
        closeButton:true
        })
    })
  }

/* -------------------------------------------------------------------------- */
/*                              Display All Users                             */
/* -------------------------------------------------------------------------- */

  displayRoles(){
    this.rolesService.getRoles().subscribe(data =>{
      this.rows = data
    },err =>{
      this.toster.error("Problem To Get Data Please Try Again Later",'', {
        timeOut: 3000,
        positionClass: 'toast-top-full-width',
        closeButton:true
    })
  })
}
}
