import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {environment} from '../../environments/environment';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class JobListService {
  baseUrl =environment.baseURl;
  constructor(private http:HttpClient) { }
  id;
  getAllJobs(): Observable<any> {
    // Real End Point => tests/find/candidate
    return this.http.get(this.baseUrl+'jobs/');
  }
  addJob(JobObj): Observable<any> {
    // Real End Point => tests/find/candidate
    return this.http.post(this.baseUrl+'jobs/',JobObj);
  }
  EditJob(JobObj): Observable<any> {
    // Real End Point => tests/find/candidate
    return this.http.put(this.baseUrl+'jobs/'+JobObj.id, JobObj);
  }
  deleteJob(id): Observable<any> {
    // Real End Point => tests/find/candidate
    return this.http.delete(this.baseUrl+'jobs/'+id);
  }

}
