import {Component, OnInit, ViewChild} from '@angular/core';
import {FormControl, FormGroup, NgForm, Validators} from "@angular/forms";
import { RoleService } from 'services/user-managment-services/role.service';
import { ProfileService } from 'services/user-managment-services/profile.service';
import {Profil} from './../../../../../models/user-managment-models/ProfilRequest';
import { ToastrService } from 'ngx-toastr';
import {Location} from '@angular/common'
@Component({
  selector: 'app-add-profile',
  templateUrl: './add-profile.component.html',
  styleUrls: ['./add-profile.component.scss']
})
export class AddProfileComponent implements OnInit {

roles;
isSelected ="type"
isLoading= false;
  @ViewChild('f') floatingLabelForm: NgForm;
  @ViewChild('vform') validationForm: FormGroup;
  regularForm: FormGroup;

/* -------------------------------------------------------------------------- */
/*                                 Constructor                                */
/* -------------------------------------------------------------------------- */

constructor(private roleService:RoleService,private profileService:ProfileService,private toast:ToastrService,private _location:Location) { }

/* -------------------------------------------------------------------------- */
/*                             NgONInt LifeCyclce                             */
/* -------------------------------------------------------------------------- */

  ngOnInit() {
    const alldata = []
    this.roleService.getRoles().subscribe(roles =>{
      roles.map(data =>{
        let role = {};
        role["id"] = data.id
        role["name"] = data.title
        alldata.push(role)
      })
      this.roles = alldata
    })

    this.regularForm = new FormGroup({
      'title': new FormControl(null, [Validators.required]),
      'description': new FormControl(null,[Validators.required]),
      'type':new FormControl(null,[Validators.required]),
      'roles':new FormControl(null,[Validators.required])
    });
  }

/* -------------------------------------------------------------------------- */
/*                               Create New User                              */
/* -------------------------------------------------------------------------- */

  onReactiveFormSubmit() {
    if(this.regularForm.valid){
      this.isLoading = true ;
      const newProfile = new Profil(this.regularForm.value.title,this.regularForm.value.description,this.regularForm.value.type,this.regularForm.value.roles)
      this.profileService.createProfile(newProfile).subscribe(data =>{
        this.isLoading = false;
        this.toast.success("Profile Added",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
        this.regularForm.reset();
      },err =>{
        this.isLoading = false;
        this.toast.error("Profile Error",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
      })
    }
  }
  goback(){
    this._location.back()
  }
}
