import {Component, OnInit, ViewChild} from '@angular/core';
import {DatatableComponent} from '@swimlane/ngx-datatable';
import {TranslateService} from '@ngx-translate/core';
import { ProfileService } from 'services/user-managment-services/profile.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-list-profils',
  templateUrl: './list-profils.component.html',
  styleUrls: ['./list-profils.component.scss']
})
export class ListProfilsComponent implements OnInit {

  FilterOptionLabel = 'Filter Option';
  FilterPlaceHolder = 'Type to filter the name column...';

  rows = [];

  temp = [];

  // Table Column Titles
  columns = [
    { prop: 'type' },
    { prop: 'profile' },
  ];
  @ViewChild(DatatableComponent) table: DatatableComponent;

/* -------------------------------------------------------------------------- */
/*                                 Constructor                                */
/* -------------------------------------------------------------------------- */

  constructor(public translate: TranslateService,private profileService:ProfileService,private route:Router,private toster:ToastrService){

    this.temp = [
    ];
    this.rows = this.temp;
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    console.log(val)
    // filter our data
    const temp = this.temp.filter(function (d) {
      console.log(d)
      return d.Profile.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rows = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

/* -------------------------------------------------------------------------- */
/*                              NgInit LifeCycle                              */
/* -------------------------------------------------------------------------- */

  ngOnInit(): void {
    this.displayProfiles()
  }

/* -------------------------------------------------------------------------- */
/*                          Navigate TO Another Pages                         */
/* -------------------------------------------------------------------------- */

  getElement(id){
    this.route.navigate(['/pages','login','list-profils','update-profile',id])
  }

  seeDetails(id){
    this.route.navigate(['/pages','login','list-profils','details-profile',id])
  }
/* -------------------------------------------------------------------------- */
/*                                 Delete User                                */
/* -------------------------------------------------------------------------- */

  deleteProfile(id){
    console.log(id)
    this.profileService.deleteProfile(id).subscribe(data => {
      this.toster.success("Profile Deleted",'', {
        timeOut: 3000,
        positionClass: 'toast-top-full-width',
        closeButton:true
        })
      this.displayProfiles()
    },err =>{
      this.toster.error("Wrong Move",'', {
        timeOut: 3000,
        positionClass: 'toast-top-full-width',
        closeButton:true
        })
    })
  }
/* -------------------------------------------------------------------------- */
/*                              display Profiles                              */
/* -------------------------------------------------------------------------- */
displayProfiles(){
  this.profileService.getProfiles().subscribe(data =>{
    let Roles = [];
    data.map(fixing =>{
      let roleWithNewName ={}
      roleWithNewName["profile"] = fixing.title;
      roleWithNewName["type"] =  fixing.type_profile;
      roleWithNewName["id"] = fixing.id;
      roleWithNewName["description"] = fixing.description
      Roles.push(roleWithNewName)
    })
    this.rows = Roles
  })
}


}
