import {Component, OnInit, ViewChild} from '@angular/core';
import {FormControl, FormGroup, NgForm, Validators} from "@angular/forms";
import { ProfileService } from 'services/user-managment-services/profile.service';
import { RoleService } from 'services/user-managment-services/role.service';
import { ActivatedRoute } from '@angular/router';
import {Location} from "@angular/common";
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-update-profile',
  templateUrl: './update-profile.component.html',
  styleUrls: ['./update-profile.component.scss']
})
export class UpdateProfileComponent implements OnInit {

  roles;
  isLoading = false;
  @ViewChild('f') floatingLabelForm: NgForm;
  @ViewChild('vform') validationForm: FormGroup;
  regularForm: FormGroup;
/* -------------------------------------------------------------------------- */
/*                                 Constructor                                */
/* -------------------------------------------------------------------------- */

constructor(private roleService:RoleService,private profileService:ProfileService,private route:ActivatedRoute,private _location:Location,private toster:ToastrService) { }

/* -------------------------------------------------------------------------- */
/*                          Ng OnInit LifeCycle hook                          */
/* -------------------------------------------------------------------------- */

  ngOnInit() {
    this.isLoading = true
    const alldata = []
    this.roleService.getRoles().subscribe(roles =>{
      roles.map(data =>{
        let role = {};
        role["id"] = data.id
        role["name"] = data.title
        console.log(role)
        alldata.push(role)
      })
      this.roles = alldata
      this.isLoading = false
    })

    this.regularForm = new FormGroup({
      'title': new FormControl(null, [Validators.required]),
      'description': new FormControl(null,[Validators.required]),
      'type':new FormControl(null,[Validators.required]),
      'roles':new FormControl(null,[Validators.required])
    });
    this.profileService.getProfileById(this.route.snapshot.params['id']).subscribe(data =>{
      this.regularForm.setValue({title:data.title,description:data.description,type:data.type_profile,roles:data.roles})
    },err =>{
      this.toster.error("We Have Problem Please Try Again",'', {
        timeOut: 3000,
        positionClass: 'toast-top-full-width',
        closeButton:true
        })
        this.goback() 
      })
      
  }

/* -------------------------------------------------------------------------- */
/*                               Update Profile                               */
/* -------------------------------------------------------------------------- */

  onReactiveFormSubmit() {
    if(this.regularForm.valid){
      this.isLoading = true
      const updateProfile = {
        "title":this.regularForm.value.title,
        "description":this.regularForm.value.description,
        "type_profile":this.regularForm.value.type,
        "roles":this.regularForm.value.roles,
        "id":this.route.snapshot.params['id']
      }
      this.profileService.updateProfile(updateProfile).subscribe(data =>{
        this.toster.success("User Updated",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
      this.goback()
      },err =>{
        this.toster.error("Wrong Move",'', {
          timeOut: 3000,
          positionClass: 'toast-top-full-width',
          closeButton:true
          })
          this.isLoading = false
        })
    }
     
  }

/* -------------------------------------------------------------------------- */
/*                            Go Back To Last Page                            */
/* -------------------------------------------------------------------------- */

  goback(){
    this._location.back()
  }





}
