import { Component, OnInit, ViewChild } from "@angular/core";
import { DatatableComponent } from "@swimlane/ngx-datatable";
import { FormBuilder, Validators, FormGroup } from "@angular/forms";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import { ToastrService } from "ngx-toastr";
import { Location } from "@angular/common";
import { data } from "app/shared/data/smart-data-table";
import { JobListService } from "../../../../services/job/jobs-list.service";
import { DepartmentService } from 'services/department/department.service';
import { Router } from '@angular/router';

import Swal from 'sweetalert2';
import { LogsService } from 'services/job/logs.service';
declare let swal: any;

@Component({
  selector: "app-job-list",
  templateUrl: "./job-list.component.html",
  styleUrls: ["./job-list.component.scss"],
})
export class JobListComponent implements OnInit {
  JobForm: FormGroup;
  jobEditForm: FormGroup;
  departmentList =[];
  closeResult: string;
  Joblist = [];
  rows = [];
  newJob;
  temp = [];
  resetRows = [];
  editId
  // Table Column Titles
  columns = [
    { prop: "Job Name" },
    { prop: "department" },
    // { prop: "description" },
    { prop: "Created date" },
    { prop: "city" },
    { prop: "country" },
    { prop: "Min Salary" },
    { prop: "Max salary" },
    /* { prop: "Compatibilty of Matching" }, */
  ];
  @ViewChild(DatatableComponent) table: DatatableComponent;

  constructor(
    private formBuilder: FormBuilder,
    private modalService: NgbModal,
    private toster: ToastrService,
    private _location: Location,
    private departmenService: DepartmentService,
    private jobListService: JobListService,
    private router:Router,
    private logs:LogsService,
  ) {}

  ngOnInit(): void {
    this.getDepartments();
    this.getJobs();

    this.JobForm = this.formBuilder.group({
      description: ["", Validators.required],
      Jobname: ["", Validators.required],
      contractType: ["", Validators.required],
      city: ["", Validators.required],
      country: ["", Validators.required],
      salary_from: ["", Validators.required],
      salary_to: ["", Validators.required],
      department: ["", Validators.required],
    });

    this.jobEditForm = this.formBuilder.group({
      description: ["", Validators.required],
      Jobname: ["", Validators.required],
      contractType: ["", Validators.required],
      city: ["", Validators.required],
      country: ["", Validators.required],
      salary_from: ["", Validators.required],
      salary_to: ["", Validators.required],
      department: ["", Validators.required],
    });
  }
  updateFilter(event) {
    const val = event.target.value;
    console.log(val);

    this.rows = this.resetRows
    // filter our data
    let temp = this.rows.filter(function (e) {
      return e["department"].includes(val) || e["Job Name"].includes(val) 
    });
    console.log(temp);
    console.log(this.rows);


    // update the rows
    this.rows = temp
    if (val == "") {
      this.rows = this.resetRows
    }
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  /* -------------------------------------------------------------------------- */
  /*                           Modal Functions start                          */
  /* -------------------------------------------------------------------------- */
  open(content) {
    this.modalService.open(content, { size: "lg" }).result.then(
      (result) => {
        this.closeResult = `Closed with: ${result}`;
      },
      (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      }
    );
  }

  // This function is used in open
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return "by pressing ESC";
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return "by clicking on a backdrop";
    } else {
      return `with: ${reason}`;
    }
  }

  /* -------------------------------------------------------------------------- */
  /*                                Submit Modal Form                           */
  /* -------------------------------------------------------------------------- */

  /* -------------------------------------------------------------------------- */
  /*                               start get jobs                         */
  /* -------------------------------------------------------------------------- */
  getJobs() {
    this.jobListService.getAllJobs().subscribe(
      (data) => {
        console.log(data);

        this.Joblist = data;
        this.rows = data;
        for (let i = 0; i < this.rows.length; i++) {
          this.rows[i]["Job Name"] = this.rows[i]["Jobname"];
          this.rows[i]["Min Salary"] = this.rows[i]["salary_to"];
          this.rows[i]["Max salary"] = this.rows[i]["salary_from"];
          this.rows[i]["Created date"] = this.rows[i]["date"];
        }
        this.resetRows = this.rows
      },
      (err) => {
        this.toster.error("We Have Problem Please Try Again", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }
  /* -------------------------------------------------------------------------- */
  /*                                end get jobs                           */
  /* -------------------------------------------------------------------------- */

  onSubmit(form) {
    console.log(form.value);
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, "0");
    var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
    var yyyy = today.getFullYear();
    var date = mm + "-" + dd + "-" + yyyy;
    // console.log(date);

    // this.Joblist.push(form.value)
    this.modalService.dismissAll();
    this.newJob = {
      description: form.value.department,
      Jobname: form.value.Jobname,
      contractType: form.value.contractType,
      city: form.value.city,
      country: form.value.country,
      salary_to: form.value.salary_from,
      salary_from: form.value.salary_to,
      department: form.value.department,
      date: date,
    };


/* add logs */
    let logsObj={
      full_name:"Mohamed Ahmed",
      username:"Mohamed Ahmed",
      type_user:"recruiter",
      description:`Added New Job "${this.newJob.Jobname}"`,
      profile:"",
      created: date,
      type_activity:"Add Job"
  }
  this.logs.addLogs(logsObj).subscribe()


    this.jobListService.addJob(this.newJob).subscribe(
      (data) => {
        console.log(data);
        this.rows = data;
        this.resetRows = this.rows

        this.toster.success(" added successfully", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
        this.getJobs();
        this.JobForm.reset();
        this.modalService.dismissAll();
      },
      (err) => {
        this.toster.error("We Have Problem Please Try Again", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }

  goback() {
    this._location.back();
  }

  /* -------------------------------------------------------------------------- */
  /*                      Submit TO modal create job form                      */
  /* -------------------------------------------------------------------------- */

  removeJob(value){
        // console.log(data);
        Swal.fire({
          title: 'Are you sure?',
          text: "You won't be able to revert this!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
          if (result.value) {
            Swal.fire(
              'Deleted!',
              'Job has been deleted.',
              'success'
            )

              /* add logs */
              var today = new Date();
              var dd = String(today.getDate()).padStart(2, '0');
              var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
              var yyyy = today.getFullYear();
              var date = mm + '-' + dd + '-' + yyyy;
      
              let jobName = this.Joblist.find(x=> x.value)
              let logsObj={
                full_name:"Mohamed Ahmed",
                username:"Mohamed Ahmed",
                type_user:"recruiter",
                description:`Job deleted "${jobName.Jobname}"`,
                profile:"",
                created: date,
                type_activity:"Delete job"
            }
            this.logs.addLogs(logsObj).subscribe()

            this.jobListService.deleteJob(value).subscribe(
              (data) => {
                console.log(data);
                this.rows = data;
                this.getJobs();
              },
              (err) => {
                this.toster.error("We Have Problem Please Try Again", "", {
                  timeOut: 3000,
                  positionClass: "toast-top-full-width",
                  closeButton: true,
                });
              }
            );
          }
        })

  }
  /* -------------------------------------------------------------------------- */
  /*                      Submit TO modal create job form                      */
  /* -------------------------------------------------------------------------- */


  EditJobVal(id){
    console.log(id);
    let selectedJob = this.Joblist.filter(x=>x.id == id)
    console.log(selectedJob[0]);
    this.editId =selectedJob[0].id 
     this.jobEditForm.patchValue({
       department:selectedJob[0].department , 
       city:selectedJob[0].city, 
       country:selectedJob[0].country,
      description: selectedJob[0].description,
      Jobname: selectedJob[0].Jobname,
      contractType: selectedJob[0].contractType,
      salary_to: selectedJob[0].salary_from,
      salary_from: selectedJob[0].salary_to,
    })
  }

  onSubmitEdit(form){
   
    console.log(form);
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    var date = mm + '-' + dd + '-' + yyyy;
    console.log(date);

    var objJobEdit={
      id:this.editId,
      description: form.value.description,
      Jobname: form.value.Jobname,
      contractType: form.value.contractType,
      city: form.value.city,
      country: form.value.country,
      salary_to: form.value.salary_to,
      salary_from: form.value.salary_from,
      department: form.value.department,
      date: date,
    } 
    console.log(objJobEdit);
    

    this.jobListService.EditJob(objJobEdit).subscribe(
      (data) => {
        console.log(data);
        this.rows = data;
        this.resetRows = this.rows
        this.Joblist = data

        this.toster.success(" added successfully", "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
        this.getJobs();
        this.JobForm.reset();
        this.modalService.dismissAll();
      },
      (err) => {
        console.log(err)
        this.toster.error(err.message, "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }

  getDepartments() {
    this.departmenService.getDepartment().subscribe(
      (data) => {
        console.log(data);
        this.departmentList = data;
        this.rows = data;
        this.resetRows = this.rows
      },
      (err) => {
        console.log(err)
        this.toster.error(err, "", {
          timeOut: 3000,
          positionClass: "toast-top-full-width",
          closeButton: true,
        });
      }
    );
  }

  viewJobDetails(value){
    this.router.navigate(["pages","login","jobs","info",value,"details"])
    this.jobListService.id = value

  }
}
