import { Injectable } from '@angular/core';
import {environment} from '../../environments/environment';
import {Observable} from 'rxjs';

import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class ProfileService {

  baseUrl =environment.baseURl;

  constructor(private http:HttpClient) { }

  getProfiles(): Observable<any> {
    return this.http.get(this.baseUrl+'profiles');
   }
   
  getProfileById(id: any): Observable<any> {
    return this.http.get(this.baseUrl+'profiles'+ '/' + id);
  }

  createProfile(profile): Observable<any> {
    return this.http.post(this.baseUrl+'profiles', profile);
  }

  updateProfile(profile): Observable<any> {
   return this.http.put(this.baseUrl + 'profiles/' + profile.id , profile);
  }

  deleteProfile(id: number) {
    return this.http.delete(this.baseUrl+'profiles'+ '/' + id );
  }

}
