import { Router } from "@angular/router";
import { Injectable } from "@angular/core";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { catchError, tap } from "rxjs/operators";
import { throwError, BehaviorSubject } from "rxjs";
import { userToken } from "app/shared/models/userToken";
import { environment } from "../../../environments/environment";
@Injectable({ providedIn: "root" })
export class AuthService {
  user = new BehaviorSubject<userToken>(null);
  token: any;
  baseUrl = environment.baseURl;

  private tokenExpirationTimer: any;
  constructor(private http: HttpClient, private router: Router) {}
  /* -------------------------------------------------------------------------- */
  /*                   Register Function And Return The Token                   */
  /* -------------------------------------------------------------------------- */

  signupUserAsCandiate(
    username: string,
    password: string,
    confirmPassword: string,
    domin: string,
    phoneNumber: number,
    email: string,
  ) {
    //your code for signing up the new user
    return this.http
      .post(`${this.baseUrl}users`, {
        "username": username,
        "password": password,
        "confirmPassword": confirmPassword,
        "domin": domin,
        "phoneNumber": phoneNumber,
        "email": email,
        "about": "",
        "first_name": "",
        "last_name": "",
        "job_title": "",
        "birth_date": "",
        "experience": "",
        "current_salary": "",
        "expected_salary_max": "",
        "expected_salary_min": "",
        "education_level": "",
        "language": "",
        'description': "",
        "country": "",
        "city": "",
        "address": '',
        "website": "",
        "profile_image": "",
        "social_networks":  {
          "facebook": "",
          "linkedin": ""
        }
      })
      .pipe(
        catchError(this.handleError),
        tap((resData) => {
          this.handleAuthcation(resData["accessToken"]);
        })
      );
  }
  signupUserRecruiter(
    company: string,
    password: string,
    confirmPassword: string,
    domin: string,
    phoneNumber: number,
    email: string
  ) {
    //your code for signing up the new user
    return this.http
      .post(`${this.baseUrl}users`, {
        email: email,
        password: password,
        repeat_password: confirmPassword,
        domain: domin,
        phone_number: phoneNumber,
        username: company,
      })
      .pipe(
        catchError(this.handleError),
        tap((resData) => {
          this.handleAuthcation(resData["accessToken"]);
        })
      );
  }

  /* -------------------------------------------------------------------------- */
  /*                     Login Function And Return The Token                    */
  /* -------------------------------------------------------------------------- */

  signinUserAsCandiate(email: string, password: string) {
    //your code for checking credentials and getting tokens for for signing in user
    return this.http
      .post(`${this.baseUrl}login`, {
        email: email,
        password: password,
      })
      .pipe(
        catchError(this.handleError),
        tap((resData) => {
          this.handleAuthcation(resData["accessToken"]);
        })
      );
  }
  signinUserAsRecruiter(email: string, password: string) {
    //your code for checking credentials and getting tokens for for signing in user
    return this.http
      .post(`${this.baseUrl}login`, {
        email: email,
        password: password,
      })
      .pipe(
        catchError(this.handleError),
        tap((resData) => {
          this.handleAuthcation(resData["accessToken"]);
        })
      );
  }
  autoLogin() {
    const userData: {
      _token: string;
      _tokenExpirationDate: string;
    } = JSON.parse(sessionStorage.getItem("token"));
    if (!userData) {
      return;
    }
    const loadedUser = new userToken(
      userData._token,
      new Date(userData._tokenExpirationDate)
    );
    if (loadedUser.token) {
      console.log(
        (new Date(userData._tokenExpirationDate).getTime() -
          new Date().getTime()) /
          60000
      );
      this.user.next(loadedUser);
      this.autoLogout(
        new Date(userData._tokenExpirationDate).getTime() - new Date().getTime()
      );
    }
  }
  /* -------------------------------------------------------------------------- */
  /*                     Logout Function TO remove The Token                    */
  /* -------------------------------------------------------------------------- */

  logout() {
    this.user.next(null);
    window.sessionStorage.removeItem("token");
    this.router.navigate([""]);
    if (this.tokenExpirationTimer) {
      clearTimeout(this.tokenExpirationTimer);
    }
    this.tokenExpirationTimer = null;
    // this.token = null;
  }
  autoLogout(expirationDate: number) {
    this.tokenExpirationTimer = setTimeout(() => {
      this.logout();
    }, expirationDate);
  }

  getToken() {
    return this.token;
  }

  /* -------------------------------------------------------------------------- */
  /*         IsAuthenticated Function For Guard To return True Or false         */
  /* -------------------------------------------------------------------------- */

  isAuthenticated() {
    // here you can check if user is authenticated or not through his token
    if (window.sessionStorage.getItem("token")) {
      return true;
    }
    return false;
  }

  /* -------- Because Json Server Auth Not Responsed With Id And Email -------- */
  private handleAuthcation(token) {
    let oldTime = new Date();
    let tokenExpirationDate = new Date(oldTime.getTime() + 60 * 60000);
    const userTokenAndTime = new userToken(token, tokenExpirationDate);
    this.user.next(userTokenAndTime);
    this.autoLogout(tokenExpirationDate.getTime() - new Date().getTime());
    sessionStorage.setItem("token", JSON.stringify(userTokenAndTime));
  }

  /* -------------------------------------------------------------------------- */
  /*             Handling The Responsed Error In login And Register             */
  /* -------------------------------------------------------------------------- */

  private handleError(errorRes: HttpErrorResponse) {
    let ErrorMessage = "An unkown error occurred!";
    if (!errorRes.error) {
      return throwError(ErrorMessage);
    }
    switch (errorRes.error) {
      case "Email format is invalid":
        ErrorMessage = "This email is exists";
        break;
      case "Cannot find user":
        ErrorMessage = "Can't find User Please Try Again";
        break;
      case "Incorrect password":
        ErrorMessage = "Wrong Password";
        break;
    }
    return throwError(ErrorMessage);
  }
}
